* {
  text-rendering: geometricPrecision; // OK on chrome
}

html {
  width: 100%;
  position: relative;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  position: relative;
  overflow-x: hidden;
  width: 100vw;
  background-color: black;
  color: black;
  font-size: 0; // to fix space at the end
  &.media-open {
    overflow: hidden;
  }
}

main {
  background-color: none;
}

hr {
  border-top: 0.5px solid rgba(0, 0, 0, 0.1);
}

section {
  max-width: 100vw;
  overflow: hidden;
}

a {
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 10%;
  color: inherit;
  @at-root body:not([data-user-can-touch="true"]) {
    transition: color 200ms;
  }
  &:hover {
    color: var(--color24-accent);
  }
  cursor: pointer;
}

em,
strong {
  font-style: normal;
  font-weight: 900;
}

i.fontello {
  font-family: fontello;
  font-style: normal;
  font-weight: normal;
  position: relative;
  top: 0.1em;
}

.page-body {
  position: relative;
  padding-top: #{$space * 4};
  padding-bottom: #{$space * 1.5};
  background-color: white;

  &[data-has-children="yes"] {
    background-color: transparent;
  }
  @at-root body[data-template="home"] & {
    background-color: transparent;
  }

  .block {
    &:not([data-marginbottom="false"]):not(.tr_space) {
      padding-bottom: 120px;
    }
  }

  .block.textImgCta {
    figure,
    img {
      position: relative;
      width: 100%;
    }
  }

  .block.multicolumns {
    figure,
    img {
      position: relative;
      width: 100%;
    }
  }

  .block.accordion {
    .items {
      .item {
        margin-left: -10px;
        margin-right: -10px;
        padding: 10px;
        border: 1px solid black;
        border-radius: $border-radius;
        margin-bottom: 10px;
        &.open {
          transform: margin-bottom 200ms, margin-top 200ms;
          color: var(--color24-accent);
          color: white;
          background-color: $dark;
          .title {
            color: var(--color24-accent);
            .arrow {
              transform: rotate(45deg);
            }
          }
        }

        .title {
          @include font-m;
          font-weight: 900;
          cursor: pointer;
          display: flex;
          align-items: baseline;
          justify-content: space-between;
          &:hover {
            color: var(--color24-accent);
          }
          .arrow {
            font-weight: 400;
            transition: transform 200ms;
          }
        }
        .content {
          @include font-s;
          p {
            @include font-s;
            max-width: 60em;
            padding-top: 1em;
          }
        }
      }
    }
  }
}

.colored-bold p {
  em,
  strong {
    color: var(--color24-accent);
  }
}

.kt-paragraphs {
  p {
    margin-bottom: 0.8em;
    em,
    strong {
      font-style: normal;
      font-weight: 900;
    }
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 20px 0;
    padding: 0 0 0 0;
    font-weight: 900;
  }
  .shwa {
    position: relative;
    display: inline-block;
    transform: rotate(180deg) translateY(-0.23em);
  }
}

.details-item .title .shwa {
  transform: rotate(180deg);
  position: relative;
  display: inline-block;
  top: 0.22em;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: $button-h;
  position: relative;
  @include font-s;
  font-weight: 900;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0 #{$space};
  transition: all 100ms;
  background-color: black;
  color: white;
  border-radius: $border-radius;
  &:hover,
  &.active {
    color: var(--color24-accent) !important;
  }
  &.ticket {
    padding: 0 #{$space * 0.7};
    height: $button-h-s;
    text-transform: none;
    border: 1px solid rgba(255, 255, 255, 0.5);
    &.disabled {
      border: 1px solid rgba(255, 255, 255, 0.5);
      color: white;
    }
  }
  &.large {
    height: $button-h-l;
    @include font-m;
    // font-weight: 900;
    text-transform: none;
    padding: 0 #{$space};
    &::after {
      transform: scale(1.4);
      left: 10px;
    }
    text-transform: none;
  }
  &.disabled {
    background-color: transparent;
    color: black;
    pointer-events: none;
    border: 1px solid black;
  }
}
.button + .button {
  margin-left: 10px;
}

.button-bling {
  $layer-dist: 5px;
  $layer-dist-hover: 3px;
  // border: 1px solid red;
  padding-top: #{$layer-dist * 4};
  padding-right: #{$layer-dist * 4};
  // background-color: red;
  position: relative;
  display: inline-block;
  padding-bottom: #{$layer-dist * 4};
  transition: padding-bottom 200ms;
  margin-top: 20px;
  margin-bottom: 0px;
  .layer {
    position: absolute;
    width: calc(100% - #{$layer-dist * 4});
    height: calc(100% - #{$layer-dist * 4});
    background-color: $dark;
    border: 0.5px solid var(--color24-accent);
    border-radius: $border-radius;
    transition: margin 200ms;
    &.one {
      margin-left: #{$layer-dist * 0};
      margin-top: -#{$layer-dist * 0};
    }
    &.two {
      margin-left: #{$layer-dist * 1};
      margin-top: -#{$layer-dist * 1};
    }
    &.three {
      margin-left: #{$layer-dist * 2};
      margin-top: -#{$layer-dist * 2};
    }
    &.four {
      margin-left: #{$layer-dist * 3};
      margin-top: -#{$layer-dist * 3};
    }
    &.last {
      position: relative;
      margin-left: #{$layer-dist * 4};
      margin-top: -#{$layer-dist * 4};
      margin-right: -#{$layer-dist * 4};
      width: auto;
      height: auto;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      height: $button-h-bl;
      @include font-m;
      font-weight: 900;
      text-decoration: none;
      padding: 0 #{$space * 2};
      background-color: var(--color24-accent);
      color: black;
      border-radius: $border-radius;
    }
  }
  &:hover,
  &:active {
    padding-bottom: #{$layer-dist-hover * 4};
    .layer {
      &.one {
        margin-left: #{$layer-dist-hover * 0};
        margin-top: -#{$layer-dist-hover * 0};
      }
      &.two {
        margin-left: #{$layer-dist-hover * 1};
        margin-top: -#{$layer-dist-hover * 1};
      }
      &.three {
        margin-left: #{$layer-dist-hover * 2};
        margin-top: -#{$layer-dist-hover * 2};
      }
      &.four {
        margin-left: #{$layer-dist-hover * 3};
        margin-top: -#{$layer-dist-hover * 3};
      }
      &.last {
        position: relative;
        margin-left: #{$layer-dist-hover * 4};
        margin-top: -#{$layer-dist-hover * 4};
        margin-right: -#{$layer-dist-hover * 4};
      }
    }
  }
}

.program-filters {
  margin-left: -10px;
  margin-right: -10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
  .input-group {
    margin-left: 10px;
    margin-right: 10px;
  }
}

.radio-group {
  display: inline-block;
  margin-bottom: 10px;
  .button {
    margin-left: -1px;
    border-radius: 0;
    border: 1px solid black;
    // font-weight: 400;
    @include media-breakpoint-down(sm) {
      padding-left: 8px;
      padding-right: 8px;
    }
    &:first-of-type {
      border-top-left-radius: $border-radius;
      border-bottom-left-radius: $border-radius;
    }
    &:last-of-type {
      border-top-right-radius: $border-radius;
      border-bottom-right-radius: $border-radius;
    }
    &:not(.selected) {
      color: black;
      background-color: transparent;
      border: 1px solid black;
    }
    &.selected {
      pointer-events: none;
    }
    &:hover {
      color: black !important;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }
}

.button.tag {
  text-transform: none;
  margin-bottom: 10px;
}

.btn-select {
  display: inline-block;
  position: relative;
  .button.off {
    position: relative;
    z-index: 3;
    border: 1px solid black;
    background-color: transparent;
    color: black;
  }
  .button.selected {
    position: relative;
    z-index: 3;
    font-weight: 400 !important;
    text-transform: none !important;
    &::after {
      content: "×";
      display: inline-block;
      margin-left: 7px;
      margin-right: -7px;
      color: var(--color23-one);
    }
  }
  .dropdown {
    z-index: 4;
    position: absolute;
    opacity: 0;
    pointer-events: none;
    transition: opacity 200ms;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    border: 1px solid black;
    border-radius: $border-radius;
    background-color: var(--color23-one);
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: flex-start;
    padding: 7px 20px 10px 20px;
    @include font-s;
    a {
      margin: 7px 0;
    }
  }
  .sensi-close {
    opacity: 0;
    pointer-events: none;
    transition: opacity 200ms;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.15);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &.open {
    .dropdown {
      opacity: 1;
      pointer-events: auto;
    }
    .sensi-close {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

@mixin category-label {
  text-transform: capitalize;
  text-transform: uppercase;
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 1px 4px 2px 4px;
  border-radius: 2px;
  z-index: 1;
  @include font-xs;
  font-weight: 900;
}
.event-preview {
  overflow: hidden;
  border-radius: $border-radius;

  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: stretch;

  margin-bottom: 20px;

  .image {
    position: relative;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    .category {
      @include category-label;
    }
  }
  .texts {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    background-color: black;
    color: white;
    padding: 12px;
    @include font-s;
    .event-title {
      font-weight: 900;
    }
  }

  &[data-type="program-grid"] {
    margin-bottom: 100px;
    @include media-breakpoint-down(sm) {
      margin-bottom: 50px;
    }
    .image {
      padding-bottom: 100%;
    }
    .texts .info {
      .arrow-prev,
      .arrow-next {
        display: none;
      }
      .date-time {
        text-align: right;
        .date {
          font-weight: 900;
        }
        @include media-breakpoint-down(xs) {
          display: none;
        }
      }
    }
  }

  &[data-type="nav-prev"],
  &[data-type="nav-next"] {
    .image {
      padding-bottom: 56.25%;
    }
    .texts .info .date-time {
      display: none;
    }
    .texts br.newline {
      display: none;
    }
    .texts .titles {
      white-space: nowrap;
      overflow: hidden;
      position: relative;
      flex-grow: 1;
      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 20px;
        background: linear-gradient(
          to right,
          rgba(black, 0) 0%,
          rgba(black, 1) 50%,
          rgba(black, 1) 100%
        );
      }
    }

    @include media-breakpoint-down(sm) {
      .texts br.newline {
        display: block;
      }
      .image {
        padding-bottom: 100%;
      }
      .texts {
        flex-flow: column nowrap !important;
        justify-content: space-between !important;
        .titles {
          margin-left: 0 !important;
          margin-right: 0 !important;
          width: 100%;
        }
        .info {
          margin-top: 20px;
        }
      }
    }

    &[data-type="nav-prev"] {
      .texts {
        flex-flow: row-reverse;
        justify-content: flex-end;
      }
      .texts .titles {
        margin-left: 10px;
      }
      .texts .info .arrow-next {
        display: none;
      }
    }
    &[data-type="nav-next"] {
      .texts .titles {
        margin-right: 10px;
      }
      .texts .info .arrow-prev {
        display: none;
      }
      @include media-breakpoint-down(sm) {
        .texts {
          align-items: flex-end !important;
        }
      }
    }
  }

  &[data-type="small"],
  &[data-type="small-soft"] {
    $height: 90px;
    flex-flow: row nowrap;
    height: $height;
    .image {
      width: $height;
      min-width: $height;
      .category {
        display: none;
      }
      background-color: black;
    }
    .texts {
      flex-grow: 1;
      .info {
        display: none;
      }
    }
  }

  &[data-type="small-soft"] {
    .texts {
      color: white;
      background-color: black;
    }
  }

  &[data-is-past="yes"] {
    .image {
      filter: grayscale(1);
      opacity: 0.4;
    }
  }
}
.event-preview[data-category="performances"] .image .category {
  color: black;
  background-color: $yellow24;
}
.event-preview[data-category="experiences"] .image .category {
  color: $yellow24;
  background-color: black;
}

@mixin details-item-style {
  @include font-s;
  margin-bottom: 1em;
  .title {
    @include font-s;
    font-weight: 900;
  }
  .text p {
    @include font-s;
    padding-right: 3.5em;
  }
}
.event-page-24 {
  margin-bottom: 100px;
  section.image {
    position: relative;
    padding-bottom: 56.25%;
    @include media-breakpoint-down(lg) {
      padding-bottom: 100%;
    }
    .event-img {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
      .credits {
        position: absolute;
        @include font-xxs;
        color: white;
        right: 10px;
        bottom: 7px;
        text-shadow: rgba(0, 0, 0, 0.8) 0 0 2px;
      }
    }
  }
  &[data-is-past="yes"] section.image .event-img {
    filter: grayscale(1);
    opacity: 0.4;
  }
  section.texts {
    position: relative;
    background-color: white;
    padding-top: #{$space * 2};
    padding-bottom: #{$space * 4};

    .details-item {
      @include details-item-style;
      &.info,
      &.star {
        .title {
          margin-left: -0.2em;
        }
      }
      &.info {
        .title::before {
          content: "▶  ";
        }
        .title {
          margin-bottom: 0.5em;
        }
      }
      &.star {
        .title::before {
          content: "★  ";
        }
      }
    }
  }
  .related {
    position: relative;
    background-color: white;
    padding-bottom: #{$space * 4};
  }
}

