.tr-modal-container {
  z-index: 30;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: opacity 800ms;
  &.show {
    opacity: 1;
    pointer-events: auto;
    .tr-modal {
      top: 0;
    }
  }
  .tr-modal-overlay {
    z-index: 1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.95);
  }
  .tr-modal {
    z-index: 2;
    position: relative;
    top: 400px;
    transition: top 800ms;
    width: 90%;
    max-height: 90vh;
    max-width: 800px;
    @include media-breakpoint-down(xs) {
      max-height: calc(100vh - 40px);
      max-width: calc(100vw - 40px);
      width: auto;
    }
    overflow-y: scroll;
    // max-height: 600px;
    background-color: white;
    .tr-title {
      position: relative;
      height: 180px;
      display: flex;
      align-items: flex-end;
      justify-content: flex-start;
      padding: $space;
      padding-bottom: 5px;
      background-position: top center;
      background-size: cover;
      background-color: $yellow24;

      @include media-breakpoint-down(xs) {
        background-color: $yellow24;
      }
      h2 {
        @include font-xl;
        color: black;
      }
      .tr-close {
        position: absolute;
        right: $space;
        top: 15px;
        @include font-s;
        // font-weight: 700;
        text-decoration: none;
      }
    }
    .tr-content {
      padding: $space;
    }
  }
}

// MARQUEE
.marquee-v5 {
  z-index: 1;
  position: relative;
  .marquee-wrapper {
    @include font-footer;
    span {
      @include font-footer;
    }
    height: 1em;
    cursor: pointer;
    &:hover .marquee span {
      color: var(--color24-accent);
    }
    &.hover-black:hover .marquee span {
      color: black;
    }
    position: relative;
  }
  --marquee-width: 100vw;
  --offset: 20vw;
  --move-initial: calc(-25% + var(--offset));
  --move-final: calc(-50% + var(--offset));
  counter-reset: menu;

  .marquee {
    position: absolute;
    top: 0;
    left: 0;
    width: var(--marquee-width);
    overflow: hidden;
    pointer-events: none;
    mix-blend-mode: color-burn;
  }

  .marquee__inner {
    width: fit-content;
    display: flex;
    position: relative;
    transform: translate3d(var(--move-initial), 0, 0);
    transform: translateX(var(--move-initial));
    animation: marquee 5s linear infinite;
    animation-play-state: paused;
    opacity: 0;
    transition: opacity 0.1s;

    &.slow {
      animation-duration: 8s;
    }
  }

  .marquee .marquee__inner {
    animation-play-state: running;
    opacity: 1;
    transition-duration: 0.4s;
  }

  .marquee span {
    text-align: center;
    white-space: nowrap;
    padding: 0 0.15em;
  }

  @keyframes marquee {
    0% {
      transform: translate3d(var(--move-initial), 0, 0);
    }

    100% {
      transform: translate3d(var(--move-final), 0, 0);
    }
  }
}

